      h1 Ajustes de cierre
    p Para realizar el cierre contable se requiere haber efectuado los procedimientos del ciclo contable, los cuales hacen referencia al proceso de reconocimiento y medicin de los hechos econmicos. Estos deben presentarse de forma ordenada, de acuerdo con el perodo de tiempo; teniendo en cuenta las normas contables, financieras, tributarias y laborales.
    p.mb-5 En el procedimiento del ciclo contable se tienen en cuenta las siguientes actividades:
    .row.align-items-center.mb-5
      .col-lg-6
        ul.lista-ul.ms-5
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Realizacin del hecho econmico.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Generacin de documentos contables.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Reconocimiento y medicin inicial del hecho econmico de acuerdo con la operacin comercial y el soporte contable generado.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Registro en el libro diario.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Traslado de la informacin al libro mayor y balances.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Elaboracin del balance de comprobacin.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Luego, se realizan los ajustes con el fin de validar la informacin y que los saldos sean correctos.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Se emite el comprobante de cierre.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Finalmente, se elaboran los estados financieros de propsito general: estado de situacin financiera, estado de resultados, estado de cambio en el patrimonio, estados de flujos de efectivo y notas.
      .col-lg-6
        figure(data-aos="flip-right")
          img(src="@/assets/curso/img2.svg", style="margin: auto; max-width: 80 %;")
    figure.mb-5
      img(src="@/assets/curso/img3.svg", style="margin: auto; max-width: 70 %;")
    p.mb-5  Los asientos de ajuste hacen referencia al reconocimiento y medicin de hechos econmicos que presentan errores, omisiones y ajustes al valor real, con el fin de reflejar la realidad econmica de la empresa y de esta manera presentar los estados financieros de forma comprensible, til, oportuna y confiable para la toma de decisiones. Estos ajustes carecen del soporte contable especfico; por lo tanto, se registran a travs de una nota de contabilidad, con el fin de evidenciar el tipo de ajustes realizado en el respectivo perodo contable.
    .row.align-items-center
