    .titulo-segundo
      h2 1.1 Deterioro
    p.mb-5 El concepto de deterioro es un trmino que se utiliza bajo estndares internacionales, teniendo en cuenta las siguientes normas y grupo de informacin:
    .row
      .col-lg-6.d-flex
        .tarjeta-avatar-b.mb-5
          img(src='@/assets/curso/img5.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              p La Norma Internacional de Contabilidad (NIC) 36 Deterioro del Valor de los Activos: esta norma es aplicable para NIIF plenas, perteneciente al grupo 1.
      .col-lg-6.d-flex
        .tarjeta-avatar-b.mb-5
          img(src='@/assets/curso/img6.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              p Seccin 27 de las NIIF para PYMES, aplicable al grupo.
    .h4 La Norma Internacional de Contabilidad (NIC) 36 Deterioro del Valor de los Activos
    p.mb-5 Esta Norma relaciona los procedimientos aplicables a los activos, con el fin de reflejar el valor real de los hechos econmicos. El deterioro hace referencia a la prdida que sufren los activos cuando el valor registrado en libros es mayor al importe recuperable; es decir, el valor al cual se pretende vender o valorar dicho activo, menos los costos y uso del bien.
    .row.justify-content-center.mb-5
      .col-lg-8
        .bloque-texto-a.color-secundario.p-4.p-md-5(data-aos="slide-left")
          .row.m-0.align-items-center.justify-content-between
            .col-lg-4.mb-4.mb-lg-0
              figure
                img(src="@/assets/curso/img7.svg", style="margin: auto; max-width: 80 %;")
            .col-lg-8
              .bloque-texto-a__texto.p-4
                p De acuerdo con la NIC 36, el importe recuperable es el mayor entre su valor razonable menos los costos de disposicin y su valor en uso (p.1695).
                  br
                  br
                  p Segn la NIIF 13 (2020) la medicin del valor razonable es el precio que se recibira por vender un activo o que se pagara por transferir un pasivo en una transaccin ordenada entre participantes de mercado en la fecha de la medicin (p.3).
    p.mb-5 El objetivo y alcance de la presente norma son los siguientes:
    .h3 El objetivo
    p.mb-5 El objetivo de esta Norma consiste en establecer los procedimientos que una entidad aplicar para asegurar que sus activos estn contabilizados por un importe que no sea superior a su importe recuperable. Un activo estar contabilizado por encima de su importe recuperable cuando su importe en libros exceda del importe que se pueda recuperar del mismo a travs de su utilizacin o de su venta. Si este fuera el caso, el activo se presentara como deteriorado, y la norma exige que la entidad reconozca una prdida por deterioro del valor de ese activo. En la Norma tambin se especifica cundo la entidad revertir la prdida por deterioro del valor, as como la informacin a revelar.
    .h4 Alcance
    p Esta Norma se aplicar para la contabilizacin del deterioro del valor de todos los activos, distintos de:
    .row.align-items-center.mb-4.mb-lg-0
      .col-lg-6
        ul.lista-ul.ms-5
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Inventarios (vase la NIC 2 Inventarios).
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Activos de contratos y activos que surgen de los costos para obtener o cumplir un contrato que se reconocen de acuerdo con la NIIF 15 Ingresos de Actividades Ordinarias procedentes de Contratos con Clientes.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Activos por impuestos diferidos (vase la NIC 12 Impuesto a las Ganancias).
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Activos que surjan por beneficios a los empleados (vase la NIC 19 Beneficios a los Empleados).
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Activos financieros dentro del alcance de la NIIF 9 Instrumentos Financieros.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Propiedades de inversin que se midan segn su valor razonable (vase la NIC 40 Propiedades de Inversin).
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Activos biolgicos relacionados con la actividad agrcola dentro del alcance de la NIC 41 Agricultura que se midan segn su valor razonable menos los costos de disposicin.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Contratos dentro del alcance de la NIIF 17 Contratos de Seguros que son activos.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Activos no corrientes (o grupos de activos para su disposicin) clasificados como mantenidos para la venta de acuerdo con la NIIF 5 Activos no Corrientes Mantenidos Para la Venta y Operaciones Discontinuadas.
      .col-lg-6
        figure(data-aos="flip-right")
          img(src="@/assets/curso/img8.svg", style="margin: auto; max-width: 70 %;")
    p Cabe resaltar, que la empresa al finalizar cada perodo contable realizar la prueba de deterioro y verificar si hay indicios de este, con el fin de realizar los ajustes a que haya lugar; para ello, realizar la comparacin del valor en libros con el importe recuperable, este proceso se debe realizar por lo menos una vez al ao.
    p.mb-5 La NIC 36, considera indicios de deterioro los que se transcriben a continuacin:
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
      .row(titulo="Fuentes externas de informacin")
        .col-12
          ul.lista-ul.ms-5
            li 
              i.fas.fa-check(style="color: #D9A171")
              | Existen indicios observables de que el valor del activo ha disminuido durante el perodo significativamente, ms que lo que cabra esperar como consecuencia del paso del tiempo o de su uso normal.
            li 
              i.fas.fa-check(style="color: #D9A171")
              | Durante el perodo han tenido lugar, o van a tener lugar en un futuro inmediato, cambios significativos con una incidencia adversa sobre la entidad, referentes al entorno legal, econmico, tecnolgico o de mercado en los que sta opera, o bien en el mercado al que est destinado el activo.
            li 
              i.fas.fa-check(style="color: #D9A171")
              | Durante el perodo, las tasas de inters de mercado, u otras tasas de mercado de rendimiento de inversiones, han experimentado incrementos que probablemente afecten a la tasa de descuento utilizada para calcular el valor en uso del activo, de forma que disminuya el importe recuperable de forma significativa.
            li 
              i.fas.fa-check(style="color: #D9A171")
              | El importe en libros de los activos netos de la entidad es mayor que su capitalizacin burstil.
      div(titulo="Fuentes internas de informacin")
        .row
          .col-12
            ul.lista-ul.ms-5
              li 
                i.fas.fa-check(style="color: #D9A171")
                | Se dispone de evidencia sobre la obsolescencia o deterioro fsico de un activo.
              li 
                i.fas.fa-check(style="color: #D9A171")
                | Durante el perodo han tenido lugar, o se espera que tengan lugar en un futuro inmediato, cambios significativos en el alcance o manera en que se usa o se espera usar el activo, que afectarn desfavorablemente a la entidad. Estos cambios incluyen el hecho de que el activo est ocioso, planes de discontinuacin o restructuracin de la operacin a la que pertenece el activo, planes para disponer del activo antes de la fecha prevista, y la reconsideracin como finita de la vida til de un activo, en lugar de indefinida.
              li 
                i.fas.fa-check(style="color: #D9A171")
                | Segn la NIC se dispone de evidencia procedente de informes internos, que indica que el rendimiento econmico del activo es, o va a ser, peor que el esperado. (p.4.)
    p.mb-5 As mismo, se establecen las #[b consideraciones de reconocimiento y medicin]:
    .row.align-items-center.mb-5
      .col-lg-6.mb-4.mb-lg-0
        figure(data-aos="flip-left")
          img(src="@/assets/curso/img9.svg", style="margin: auto; max-width: 70 %;")
      .col-lg-6
        ul.lista-ul.ms-5
