            i.fas.fa-check(style="color: #D9A171")
            | El importe del deterioro debe registrarse en el resultado del perodo, excepto los activos reconocidos por su valor revaluado de acuerdo con la Norma Internacional de Contabilidad (NIC) 16.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Para identificar el valor razonable del activo, se tiene en cuenta analizar el precio de cotizacin en el mercado activo.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | En el momento que se reconoce la prdida por deterioro se debe determinar los activos y pasivos por impuestos diferidos; para ello, se realiza la comparacin del valor en libros del activo con su base fiscal, de acuerdo con la Norma Internacional de Contabilidad (NIC 12).
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Cuando existe indicios de deterioro, es indispensable estimar el importe recuperable de forma individual o en su defecto de la unidad generadora de efectivo (conjunto de activos que generan entradas de efectivo) siempre y cuando no est por debajo del mayor valor entre los siguientes: el valor razonable menos los costos de disposicin, el valor en uso y cero.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Cuando se reconoce el deterioro de la unidad generadora del activo, se realiza la distribucin (reduccin de importe en libros) entre el conjunto de activos que lo componen.
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Se podr revertir la prdida de perodos anteriores, cuando se generan cambios en las estimaciones; por lo tanto, se aumentar su valor hasta su importe recuperable.
    .h4 Indicios para reversin de prdida por deterioro
    p.mb-5 De acuerdo con la NIC 36, al evaluar si existe algn indicio de que pueda haberse deteriorado el valor de un activo, una entidad considerar, como mnimo, los siguientes indicios:
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
      .row(titulo="Fuentes externas de informacin")
        .col-12
          ul.lista-ul.ms-5
            li 
              i.fas.fa-check(style="color: #D9A171")
              | Existen indicios observables de que el valor del activo ha aumentado significativamente durante el perodo.
            li 
              i.fas.fa-check(style="color: #D9A171")
              | Durante el perodo, han tenido, o van a tener lugar en un futuro inmediato, cambios significativos con un efecto favorable para la entidad, referentes al entorno legal, econmico, tecnolgico o de mercado en los que sta opera, o bien en el mercado al cual va destinado el activo en cuestin.
            li 
              i.fas.fa-check(style="color: #D9A171")
              | Durante el perodo, las tasas de inters de mercado, u otras tasas de mercado de rendimiento de inversiones, han experimentado disminuciones que probablemente afecten a la tasa de descuento utilizada para calcular el valor en uso del activo, de forma que incrementan el importe recuperable de forma significativa.
      div(titulo="Fuentes internas de informacin")
        .row
          .col-12
            ul.lista-ul.ms-5
              li 
                i.fas.fa-check(style="color: #D9A171")
                | Durante el perodo han tenido lugar, o se espera que tengan lugar en el futuro inmediato, cambios significativos en el alcance o manera en que se utiliza o se espera utilizar el activo, con efecto favorable para la entidad. Estos cambios incluyen los costos en los que se haya incurrido durante el perodo para mejorar o desarrollar el rendimiento del activo o reestructurar la operacin a la que dicho activo pertenece.
              li 
                i.fas.fa-check(style="color: #D9A171")
                | Se dispone de evidencia procedente de informes internos que indiquen que el rendimiento econmico del activo es, o va a ser, mejor que el esperado.
