    .h4 Deterioro en inventarios (NIC 2)
    p Esta norma se aplica a todos los inventarios; excepto instrumentos financieros y activos biolgicos. All se definen los criterios de reconocimiento, medicin, presentacin y revelacin de inventarios, permitiendo identificar el costo con base a los elementos que lo componen. De igual manera, presenta las bases para el reconocimiento y medicin del deterioro por este concepto. 
    p.mb-5 El deterioro en inventario se identifica mediante la comparacin del importe en libros y el valor neto realizable; este ltimo hace referencia al precio que se puede obtener por la venta de dicho activo. Si el importe en libros es mayor al valor neto realizable se considera deterioro de inventarios; por lo tanto, se registra la prdida mediante un gasto.
    .h4 Ejemplo:
