    .h4 Deterioro Instrumentos Financieros (NIIF) 9
    p Es importante recordar que al estudiar la Norma Internacional de Informacin Financiera (NIIF) 9 Instrumentos Financieros, se analizaron los criterios de reconocimiento, medicin, presentacin y revelacin. Esta norma tiene como objetivo establecer los principios para la informacin financiera sobre activos y pasivos financieros, de forma que se presente informacin til y relevante para los usuarios de los estados financieros para la evaluacin de los importes, calendario e incertidumbre de los flujos de efectivo futuros de la entidad.
    .row.align-items-center.mb-5
      .col-lg-6
        .bloque-texto-b.color-secundario.p-4
          .bloque-texto-b__texto
            p #[b La NIIF 9 (2014) expresa:]
            p.mb-0 Una entidad reconocer una correccin de valor por prdidas por prdidas crediticias esperadas sobre un activo financiero que se mide de acuerdo con los prrafos 4.1.2 o 4.1.2A, una cuenta por cobrar por arrendamientos, un activo de contrato o un compromiso de prstamo y un contrato de garanta financiera a los que se les aplica los requerimientos de deterioro de valor de acuerdo con los prrafos 2.1(g), 4.2.1(c) o 4.2.1(d) El objetivo de los requerimientos del deterioro de valor es reconocer las prdidas crediticias esperadas durante el tiempo de vida del activo de todos los instrumentos financieros para los cuales ha habido incrementos significativos en el riesgo crediticio desde el reconocimiento inicial  evaluado sobre una base colectiva o individual  considerando toda la informacin razonable y sustentable, incluyendo la que se refiera al futuro. (p.22)
      .col-lg-6
        figure(data-aos="flip-left").mt-4.mt-lg-0
          img(src="@/assets/curso/img11.svg", style="margin: auto; max-width: 60 %;")
    p.mb-5 El deterioro constituye la prdida en los activos financieros, el cual ha sido medido al costo o al costo amortizado; por lo tanto, se debe revisar de forma individual si existen indicios de deterioro en cada perodo contable y realizar el respectivo ajuste a que haya lugar.
    .h4 Ejemplo:
    .row.justify-content-center.mb-5
      .col-11
        .cajon.color-primario.p-4
          p.mb-0 La empresa Contable SAS, tiene una cuenta por cobrar por valor de $65.000.000, el cliente presenta una situacin econmica especial; por lo tanto, llega a un acuerdo de pago y se define que la cuenta ser pagada dentro de 6 meses, la tasa del mercado es del 1,8 % EM.
    p.mb-5 En este caso, es importante tener en cuenta que el cliente paga dentro de 6 meses el valor de $65.000.000, no se cobran intereses; por lo tanto, dicho valor se debe traer a valor presente teniendo en cuenta el concepto de matemticas financieras y valor el dinero en el tiempo. Por lo tanto, se aplica la frmula de valor actual o valor presente, mediante inters efectivo.
    .row.align-items-center
      .col-lg-6
        .cajon.color-primario.p-4.mb-5
          p VA = VF(1+i) n
          p VA = $65.000.000 / (1+0,018i)
            sup  6
          p VA = $58.401.864
        p.mb-5 El deterioro es la diferencia entre el valor actual y el valor futuro.
