    .titulo-segundo
      h2 1.2 Depreciaciones
    p La depreciacin es el desgaste que sufren los activos fijos por su uso, este se reconoce como un gasto (dbito) y se revela en el estado de resultados, disminuyendo la utilidad del ejercicio. Por otro lado, se presenta en el activo (crdito) disminuyendo el grupo de propiedades, planta y equipo.
    .row.align-items-center
      .col-lg-6
        p De acuerdo con los estndares internacionales la depreciacin se debe realizar a partir de que el activo fijo se encuentra apto para su uso, teniendo en cuenta el valor del importe en libros y la vida til estipulada en las polticas contables. La estimacin de la vida til es el perodo en el cual la empresa desea darle uso al activo fijo. 
        p Por ejemplo, la empresa Contable SAS, adquiere un vehculo para uso de la gerencia, en las polticas contables se estableci que la vida til es de tres (3) aos, teniendo en cuenta que cada tres aos venden el vehculo y adquieren uno nuevo, por lo tanto, este ser el perodo de depreciacin.
        p De igual manera, se tiene en cuenta el desgaste del activo fijo y la obsolescencia tcnica o comercial, teniendo en cuenta las mejoras, mantenimientos y reparaciones realizadas. En el caso en que la empresa, cambie las estimaciones contables respecto a la vida til o valor residual, deber revelar la informacin teniendo en cuenta los efectos que generen las modificaciones sobre los resultados futuros. Estos cambios se reconocern como un cambio en una estimacin contable de acuerdo con la NIC 8 Polticas Contables, Cambios en las Estimaciones Contables y Errores.
        p Es importante definir el valor residual, teniendo en cuenta que es un trmino utilizado frecuentemente en el procedimiento de clculo de la depreciacin de activos fijos.
        p Segn la NIC 16 (2020):
      .col-lg-6
        figure(data-aos="flip-left")
          img(src="@/assets/curso/img13.svg", style="margin: auto; max-width: 80 %;")
    .row.justify-content-center.mb-5
      .col-lg-8
        .bloque-texto-b.color-secundario.p-4
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            .h4.mb-0.ms-3 El valor residual de un activo es el importe estimado que la entidad podra obtener de un activo por su disposicin, despus de haber deducido los costos estimados para su disposicin, si el activo tuviera ya la edad y condicin esperadas al trmino de su vida til. (p.2)
              i.fas.fa-quote-right.d-flex.justify-content-end
    p De igual forma, para ampliar los conceptos sobre este tema se analizan los prrafos 43 y 44 de la NIC 16, que manifiesta que se depreciar de forma separada cada parte de un elemento de propiedades, planta y equipo que tenga un costo significativo con relacin al costo total del elemento y ser distribuida entre sus partes significativas. Por ejemplo, podra ser adecuado depreciar por separado la estructura y los motores de un avin. 
    p La depreciacin de un activo cesar cuando haya finalizado su perodo de depreciacin (vida til). Por otro lado, se tendr en cuenta la fecha ms prxima entre la clasificacin del activo como mantenido para la venta o la baja de cuentas de este, pero en ningn momento cuando el activo no se est utilizando.
    p.mb-5 Para calcular la depreciacin contable, se tiene en cuenta los siguientes elementos para hallar el importe en libros:
    figure(data-aos="flip-right").mb-5
      img(src="@/assets/curso/img14.svg", style="margin: auto; max-width: 80 %;")
    p.mb-5 En el caso que se realicen las mediciones posteriores y que el activo se revale, el nuevo importe se tendr en cuenta para la depreciacin de este. 
    .h4 Depreciacin fiscal
    p De acuerdo con el art. 69 del Estatuto Tributario (ET) para los contribuyentes declarantes de renta, se tendr en cuenta la sumatoria de los costos de adquisicin y los costos atribuibles hasta que el activo se encuentre en condiciones de uso. Cabe resaltar que los costos de desmantelamiento y retiro del elemento no se tendrn en cuenta.
    .row.mb-5
      .col-lg-6
        p #[b Pargrafo 1.] Las propiedades de inversin que se midan contablemente bajo el modelo de valor razonable, para efectos fiscales se medirn al costo.
        p Segn el art. 69-1, los activos no corrientes mantenidos para la venta tendrn en cuenta los siguientes conceptos para calcular la depreciacin acumulada:
    figure(data-aos="flip-right").mb-5
