    .h4 Vida til
    p.mb-5 La vida til de los activos fijos se relaciona en las polticas contables diseadas por la empresa, teniendo en cuenta la utilidad del activo. De acuerdo con el art. 137 del ET, la tasa de depreciacin anual y vida til equivalente se presenta de la siguiente manera:
