    .h4 Mtodos de depreciacin
    p.mb-5 La depreciacin se calcula sobre la sumatoria del precio de adquisicin, el impuesto sobre las ventas y costos atribuibles para poner en funcionamiento el activo. Los mtodos, son los siguientes:
