    p Se realiza una prueba de funcionamiento del activo y se paga $150.000. El vendedor de la mquina transfiere los riesgos y beneficios el 7 de noviembre de 2020 y la mquina est apta para ser usada el 12 de noviembre de 2020. Calcular la depreciacin del mes de noviembre y diciembre.
