    .titulo-segundo
      h2 1.3 Amortizacin
    p Este concepto se utiliza para diferir en cuotas los gastos pagados por anticipado que son consumidos en el perodo de tiempo y que efectivamente cumplen con la definicin del activo, de lo contrario se deben registrar en el gasto, clase 5.
    p Cabe resaltar que el activo debe cumplir con los siguientes criterios:
    .row.align-items-center.mb-5
      .col-lg-6
        ul.lista-ul.ms-4
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Debe ser controlado
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Proviene de eventos pasados
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Genera beneficios econmicos futuros
          li 
            i.fas.fa-check(style="color: #D9A171")
            | Debe ser fiable su valor
        p.mt-5 Algunos conceptos que se tienen en cuenta para amortizacin de gastos pagados por anticipado al final del perodo contable son:  arrendamientos, plizas de seguros, comisiones, honorarios, mantenimiento, servicios, etc.
      .col-lg-6.mt-4.mt-lg-0
        figure(data-aos="flip-left")
          img(src="@/assets/curso/img22.svg", style="margin: auto; max-width: 80 %;")
    .h4 Ejemplo:
    .row.justify-content-center.mb-5
      .col-11
        .cajon.color-primario.p-4
          p El 1 de diciembre de 2020, la empresa Contable SAS, adquiere por un ao una pliza de seguro por valor de $12.000.000, cancela en efectivo
    .row.justify-content-center.mb-5
      .col-lg-6
        table
          thead
            tr(style="background-color: #D9A171;")
              th.text-white.text-center CUENTA
              th.text-white.text-center DEBE
              th.text-white.text-center HABER
          tbody
            tr
              td Anticipo  seguros
              td $ 12.000.000
              td
            tr(style="background-color: #F6F6F6;")
              td Efectivo
              td
              td $ 12.000.000
            tr
              td Total
              td $ 12.000.000
              td $ 12.000.000
    p.mb-5 Al finalizar el mes, es decir el 31 de diciembre se realiza la amortizacin del primer mes, teniendo en cuenta trasladar el valor mensual a la cuenta de gastos-clase 5, que se calcula de la siguiente manera:
    .row.justify-content-center.mb-5
      .col-lg-6
        .cajon.color-primario.p-4
          p #[b Valor mensual= $ 12.000.000 / 12]
          p.mb-0 #[b Valor mensual= $ 1.000.000]
    p.mb-5 El valor de la pliza se divide en 12 meses, que corresponde al perodo de amortizacin. La contabilizacin del mes de diciembre se realiza como se muestra a continuacin.
    .row.justify-content-center.mb-5
      .col-lg-6
        table
          thead
            tr(style="background-color: #D9A171;")
              th.text-white.text-center CUENTA
              th.text-white.text-center DEBE
              th.text-white.text-center HABER
          tbody
            tr
              td Gastos seguros
              td $ 1.000.000
              td
            tr(style="background-color: #F6F6F6;")
              td Anticipo  seguros
              td
              td $ 1.000.000
            tr
              td Total
              td $ 1.000.000
              td $ 1.000.000
    p En el ao 2021, contina el proceso de amortizacin, una vez se haya consumido o recibido el servicio, hasta que la cuenta del activo quede en cero (0) y su valor sea trasladado al gasto, el cual se ver reflejado en el estado de resultados.
    p Por otro lado, los activos intangibles se amortizan a medida que generan beneficios econmicos para la empresa, teniendo en cuenta que estos poseen vida til definida e indefinida, estos ltimos de acuerdo con la NIC 38 no se amortizan; adems se tendr en cuenta los criterios relacionados en la NIC 38 y polticas diseadas por la empresa. Cabe resaltar, que los activos intangibles inician el proceso de amortizacin, una vez este se encuentren en condiciones de uso.
    p La NIC 38 (2020):
    .row.align-items-center.mb-5
      .col-lg-6
        .bloque-texto-b.color-secundario.p-4
          .bloque-texto-b__texto
            p.mb-0 La amortizacin cesar en la fecha ms temprana entre aquella en que el activo se clasifique como mantenido para la venta (o incluido en un grupo de activos para su disposicin que se haya clasificado como mantenido para la venta) de acuerdo con la NIIF 5, y la fecha en que se produzca la baja en cuentas de este. (prr.97)
              br
              br
              p Pueden utilizarse diferentes mtodos para distribuir el importe amortizable de un activo, de forma sistemtica, a lo largo de su vida til. Estos mtodos incluyen el mtodo lineal, el de depreciacin decreciente y el de las unidades de produccin. El mtodo utilizado se seleccionar a partir del patrn esperado de consumo de los beneficios econmicos futuros esperados incorporados en el activo y se aplicar de forma coherente de perodo a perodo, a menos que se produzca un cambio en los patrones esperados de consumo de dichos beneficios econmicos futuros. (prr.98)
      .col-lg-6.mt-4.mt-lg-0
        figure(data-aos="flip-right")
          img(src="@/assets/curso/img23.svg", style="margin: auto; max-width: 80 %;")
    p NIC 8, establece:
    .row.justify-content-center.mb-5
      .col-lg-8
        .bloque-texto-b.color-secundario.p-4
          .bloque-texto-b__texto
            p.mb-0 Tanto el perodo como el mtodo de amortizacin utilizado para un activo intangible con vida til finita se revisarn, como mnimo, al final de cada perodo. Si la nueva vida til esperada difiere de las estimaciones anteriores, se cambiar el perodo de amortizacin para reflejar esta variacin. Si se ha experimentado un cambio en el patrn esperado de generacin de beneficios econmicos futuros por parte del activo, el mtodo de amortizacin se modificar para reflejar estos cambios. Los efectos de estas modificaciones en el perodo y en el mtodo de amortizacin, se tratarn como cambios en las estimaciones contables. (prr.106)
    .h4 Ejemplo:
    .row.justify-content-center.mb-5
      .col-lg-11
        .cajon.color-primario.p-4
          p.mb-0 El 1 de julio de 2020 la empresa Contable SAS inicia el desarrollo de un software, por lo tanto, incurre en los siguientes gastos:
    figure.mb-5(data-aos="flip-right")
      img(src="@/assets/curso/img24.svg", style="margin: auto; max-width: 80 %;")
    p.mb-5 El software estar apto para su uso el 01 de diciembre de 2020. Se estima una vida til de 3 aos. Se solicita realizar la medicin inicial y la amortizacin del primer mes.
