    .row.justify-content-center.mb-5
      .col-11
        ol.lista-ol--cuadro
          li 
            .lista-ol--cuadro__vineta
              span 1
            | Se origina una diferencia por diferencia en cambio, aumenta el valor del pasivo.
          li 
            .lista-ol--cuadro__vineta
              span 2
            | A la llegada de la mquina a puerto colombiano, el valor de la obligacin es mayor como consecuencia del incremento en la tasa de cambio. El nuevo saldo de la obligacin es de $343.500.000.  De acuerdo con la NIIF para las Pymes, el ajuste por diferencia en cambio debe incluirse en los resultados del perodo y no debe incluirse como parte del costo del activo.
