      h2 1.5 Arqueo de caja
    p Las empresas manejan la cuenta de caja general y caja menor, las cuales hacen parte del efectivo; esta ltima se utiliza para gastos de menor cuanta y debe establecerse en las polticas contables el valor del fondo y los conceptos que lo componen, con el fin de ser aplicados de forma correcta conforme los estndares establecidos. 
    p.mb-5 Por otro lado, la caja general se utiliza para cancelar gastos o conceptos mayores. En cualquiera de los dos casos es importante realizar un arqueo de caja, el cual consiste en comparar el valor de libros con lo que efectivamente existe en la caja.
    figure(data-aos="flip-right").mb-5
      img(src="@/assets/curso/img26.svg", style="margin: auto; max-width: 90 %;")
    p.mb-5 Cuando se realiza la comparacin pueden surgir diferencias que requieren un ajuste contable mediante una nota de contabilidad, estos pueden ser:
    .row.align-items-center.mb-5
      .col-auto.d-inline
        figure
          img(src="@/assets/curso/img16.svg", style="margin: auto; max-width: 60 %;")
      .col
        p.mb-0 El importe de libros es mayor al valor del arqueo de caja: eso genera un faltante, por ende, se establece el responsable de dicha situacin y se procede a realizar el ajuste, con el fin de reflejar la realidad econmica y el saldo correcto del efectivo. Los casos que se presentan por faltantes son los siguientes:
    .row.justify-content-end
      .col-11.p-4(style="background-color: #D8E4EF").mb-5
        .h4 Carece de registro en libros, sin embargo, el dinero se utiliz para cancelar algn concepto.
        .h4 Ejemplo:
        p Sandra Rojas cancel por concepto de papelera $40.000, sin embargo, no se registr dicho concepto en los libros. Por lo tanto, se realiza el ajuste, registrando el concepto pendiente.
        p Asiento de ajuste.
