    .titulo-segundo
      h2 1.6 Conciliacin bancaria
    figure(data-aos="flip-right").mb-5
      img(src="@/assets/curso/img30.svg", style="margin: auto; max-width: 50 %;")
    p La conciliacin bancaria, es el proceso de verificacin y comparacin del importe en libros de la cuenta de bancos con el extracto de la cuenta de ahorros o corriente generado por la entidad financiera; cuyo objetivo, es confrontar las diferencias y realizar los ajustes respectivos que permitan revelar informacin comprensible, til y oportuna.
    p.mb-5 La conciliacin se realiza de forma mensual, teniendo en cuenta, confirmar las fechas, valores, soportes de las transacciones, luego se analizan las diferencias encontradas y se realizan los ajustes, con el fin de reflejar el saldo correcto en los libros. Los ajustes que frecuentemente se presentan son los siguientes:
    .row.align-items-center.mb-5
