        p #[b Por notas dbito:] se presentan por concepto de chequeras, comisiones, cheques devueltos, intereses de sobregiros, intereses por prstamos y otros cobros realizados por las entidades financieras. En este caso disminuye la cuenta de bancos (crdito) y aumenta la cuenta (dbito) relacionada con el concepto que se est cancelando.
    .row.justify-content-end
