      h2 1.7 Balance de prueba
    p.mb-5 El Balance de prueba o de comprobacin es un documento que refleja los saldos en el debe y haber de las cuentas de activo, pasivo, patrimonio, ingresos, gastos y costos. Cada una de las columnas dbito y crdito deber dar sumas iguales, verificando una vez ms el principio de partida doble.
    .row.align-items-center.mb-5
      .col-lg-6
        p Las cuentas de naturaleza dbito son activos, gastos y costo y las cuentas de naturaleza crdito se encuentran conformadas por pasivo, patrimonio e ingresos.
        p Este balance es el insumo para elaborar el balance de prueba ajustado, donde se actualizan los saldos de las cuentas que presentaron ajustes por diferentes conceptos y seguidamente realizar los estados financieros de propsito general.
