      h2 1.8 Balance ajustado
    p Para elaborar el balance ajustado es importante tener en cuenta los diferentes ajustes que se presentaron en las cuentas, como son los ajustes de caja, bancos, depreciaciones, amortizaciones y dems cuentas que hayan sufrido alguna modificacin por concepto de algn tipo de ajustes, los cuales generan cambios en perodos posteriores.
    p Teniendo en cuenta lo anterior, se procede a realizar el comprobante de ajustes, seguidamente el balance ajustado donde se trasladan cada una de las cuentas de activos, pasivo, patrimonio, ingresos, gastos y costos. 
    p.mb-5 Cabe resaltar que el balance debe sumar igual en el movimiento dbito y crdito. 
