    .h4 Hoja de trabajo
    p La hoja de trabajo es el documento que recopila la informacin del balance de prueba, los ajustes, el balance ajustado, el estado de resultados y estado de situacin financiera. Su objetivo es validar la informacin contable y que sea til para la elaboracin de los estados financieros.
    p.mb-5 Cabe resaltar que este insumo contribuye en la elaboracin de los asientos de cierre, teniendo en cuenta que los saldos de las cuentas de ingresos, gastos y costos se deben cerrar al finalizar el perodo contable.
